import { buildModel } from "@/model/buildModel.js"

export default {
  data () {
    return {
      urlval: '',
      webviewStyles: {}
    }
  },
  onLoad () {
    if (new buildModel().getModel('path')) {
      let path = new buildModel().getModel('path')
      console.log(path)
      this.urlval = path
    }
  }
}
